	module.exports = {"data":{"wordpressPost":{"title":"Seven beauty items that will revolutionise your routine","content":"<p>My life is crazy hectic. I’m a single mama of two mop top boys and owner of a ragtag collection of pets. I work. I run a household. And I want to look good too but frankly, it’s the least of my priorities and when something has to be cut out of my schedule, applying makeup and doing my hair is the first thing to go. Heck, there are days I don’t even brush my teeth.</p>\n<p>What I have discovered, however, is that if I keep a snappy collection of very clever tools and cosmetics within reach, I can whip myself into decent shape in seconds &#8211; well, minutes anyway. Keen to try? I recommend this lot.</p>\n<p><strong>Tinted Moisturiser or BB Cream</strong></p>\n<p>This genius product goes on as easily as a moisturiser, yet provides the coverage of a light foundation hiding flaws such a fine lines, pigmentation and discolouration. Many also contain anti-ageing ingredients that firm skin, plus sunscreen. NB: BB stands for blemish balm.</p>\n<p>Try Burt’s Bees BB Cream SPF15, $29.95. burtsbees.com.au or 1800 813 661.</p>\n<p><strong>Hair Stylers</strong></p>\n<p>A handy hair straightener will smooth out the kinks and leave hair looking like you’ve just strolled out of the salon. Don’t forget to apply a protective heat treatment. These tools work wonders, but over time may cause damage.</p>\n<p>Remington Keratin Argan Oil Straightener, $69.95. remington-products.com.au or</p>\n<p>1800 623 118.</p>\n<p><strong>Makeup Brushes</strong></p>\n<p>When you’re in a bit of a hurry patting on powder can seem a speedy option. But the truth of the matter is you’ll never get the result that a brush will provide, says makeup artist Victoria Anderson. That means you’re likely to spend longer before you’re satisfied. Skip the hassle and use a quality brush instead.</p>\n<p>AVON Makeup Kabuki Brush, $14.99. avon.com.au or 1800 646 000.</p>\n<p><strong>Cleansing Tools</strong></p>\n<p>If you want skin that truly glows you need to cleanse it properly morning and night. A battery-operated cleansing brush will do the job better than fingertips alone. Sonic technology cleansers claim to cleanse six times betters than manual cleansing and in just 60 seconds. Others increase blood flow and boost radiance.</p>\n<p>Clarisonic Mia, $150. clarisonic.com.au or 1300 651 991</p>\n<p>Clinique Sonic System Massage Treatment Applicator, $36. clinique.com.au or 1800 556 948.</p>\n<p><strong>Lip Gloss</strong></p>\n<p>Lipstick is bold and beautiful, but lip gloss can be applied on the fly. Same goes for its cousin, lip balm. Both have come a long way in recent years and these days provide rich colour plus conditioning. Keep one in your handbag and also in the glovebox. A quick swipe and then a press of your pretty lips and you’re good to go.</p>\n<p>Lancome Juicy Shaker, $34. 1300 651 991.</p>\n<p><strong>Brow Pencils</strong></p>\n<p>There are countless brow pencils on the market and many are very, very good but if makeup application is not your forte you’ll love the new style of chunky brow pencils which allow even the most inexperienced of makeup users to achieve a beautifully shaped and defined brow.</p>\n<p>M.A.C Brows Are It Big Brow Pencil, $32. maccosmetics.com.au</p>\n<p><strong>Highlighters</strong></p>\n<p>These fabulous newcomers to the makeup world provide a natural-looking glow and give skin that youthful, fresh luminous look that we all love so much. The simplest versions come in wind-up stick form. Swipe down the bridge of the nose, the top of the cheekbones and if you’re very keen, the temples.</p>\n<p>Try Nude by Nature True Glow Highlighter Stick, $24.95. nudebynature.com.au or 1300 366 147.</p>\n<p>&nbsp;</p>\n","better_featured_image":{"source_url":"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/02/28172652/%40lacyredway.png"},"categories":[{"slug":"makeup","name":"Makeup"}]},"site":{"siteMetadata":{"title":"The Beauty Insider"}}},"pathContext":{"id":"9eab4136-2e2d-5251-b4dc-b64f1b6c712a"}}

